.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    width: 200px;
    margin: 50px;
    position: relative;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
    height: 100%;
    overflow: hidden;
  }
  
  .card > .img-container > img {
    width: 200px;
    height: 200px;
  }


